<template>
  <div>
    <b-row class="d-flex align-items-center">
      <b-col cols="12" xl="6" class="border-dashed">
        <div class="p-2">
          <b-row>
            <b-col cols="12" xl="3">
              <label class="highLightHeaderLabel">{{ $t("FILTER") }}</label>
            </b-col>
            <b-col cols="12" xl="auto">
              <label class="highLightHeaderLabel float-left">{{
                $t("MAJORCLASSIFICATION")
              }}</label>
            </b-col>
            <b-col cols="12" xl="3">
              <b-form-select></b-form-select>
            </b-col>
            <b-col cols="12" xl="2">
              <b-form-checkbox size="sm" class="mt-2">{{
                $t("USED")
              }}</b-form-checkbox>
            </b-col>
            <b-col cols="12" xl="2">
              <b-form-checkbox size="sm" class="mt-2">{{
                $t("UNUSED")
              }}</b-form-checkbox>
            </b-col>
          </b-row>
        </div>
      </b-col>
      <b-col cols="12" xl="2">
        <b-button @click="gotoAddEdit" variant="primary">{{
          $t("ADDINDUSTRYTYPE")
        }}</b-button>
      </b-col>
    </b-row>
    <br />
    <b-table
      ref="selectableTable"
      :fields="fields"
      :items="tableData"
      select-mode="single"
      sticky-header
      responsive="sm"
    >
      <!-- 一覧内容 -->
      <template v-slot:cell(selectRow)="{ rowSelected }">
        <template v-if="rowSelected">
          <input class="form-checkbox-input" type="checkbox" checked />
        </template>
        <template v-else>
          <input class="form-checkbox-input" type="checkbox" />
        </template>
      </template>
      <template v-slot:cell(Item1)="item">{{ item.value }}</template>
      <template v-slot:cell(Item2)="item">{{ item.value }}</template>
      <template v-slot:cell(Item3)="item">{{ item.value }}</template>
      <template v-slot:cell(Item4)="item">{{ item.value }}</template>
      <template v-slot:cell(Item5)="item">{{ item.value }}</template>
      <template v-slot:cell(Item6)="item">{{ item.value }}</template>
      <template v-slot:cell(Item7)="item">{{ item.value }}</template>
      <template v-slot:cell(Item8)="item">{{ item.value }}</template>
      <template v-slot:cell(Item9)="item">
        <b-button
          @click="gotoAddEdit"
          variant="primary"
          class="file-selection-btn file-btn-margin"
          >{{ item.value }}</b-button
        >
      </template>
    </b-table>
    <br />
    <b-row>
      <b-col cols="12" xl="4">
        <b-button variant="primary">{{ $t("DELINDUSTRYTYPE") }}</b-button>
      </b-col>
      <b-col cols="12" xl="5">
        <b-pagination
          id="message-list"
          v-if="lists.length != 0"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          align="center"
        ></b-pagination>
      </b-col>
      <b-col cols="12" xl="3">
        <div class="countList">
          <label>{{ $t("DISPLAY") }}</label
          >&nbsp;&nbsp;
          <label>50 | </label>
          <b-link href="javascript:;">100 | </b-link>
          <b-link href="javascript:;">150</b-link>
        </div>
      </b-col>
    </b-row>
    <br />
  </div>
</template>

<script>
export default {
  name: "CostAccountMaintanence",
  components: {},
  data() {
    return {
      tableData: [],
      selectMode: "single",
      currentPage: 1,
      perPage: 2,
    };
  },
  computed: {
    rows() {
      return this.tableData.length;
    },
    lists() {
      const items = this.tableData;
      return items.slice(
        (this.currentPage - 1) * this.perPage,
        this.currentPage * this.perPage
      );
    },
    fields() {
      return [
        {
          key: "selectRow",
          label: "",
          class: "text-center normal-width",
        },
        {
          key: "Item1",
          label: this.$t("INDUSTRYCLASSIFICATION"),
          class: "text-center normal-width",
        },
        {
          key: "Item2",
          label: this.$t("MAJORCLASSIFICATIONCODE"),
          class: "text-center normal-width",
        },
        {
          key: "Item3",
          label: this.$t("MEDIUMCLASSIFICATIONCODE"),
          class: "text-center normal-width",
        },
        {
          key: "Item4",
          label: this.$t("INDUSTRYCLASSIFICATIONNAME"),
          class: "text-center normal-width",
        },
        {
          key: "Item5",
          label: this.$t("SCALEJUDGMENTCATEGORY"),
          class: "text-center normal-width",
        },
        {
          key: "Item6",
          label: this.$t("USEFLAG"),
          class: "text-center normal-width",
        },
        {
          key: "Item7",
          label: this.$t("CREATEDDATE"),
          class: "text-center normal-width",
        },
        {
          key: "Item8",
          label: this.$t("UPDATEDATE"),
          class: "text-center normal-width",
        },
        {
          key: "Item9",
          label: this.$t("EDIT"),
          class: "text-center normal-width",
        },
      ];
    },
  },
  mounted() {
    this.getTableData();
  },

  methods: {
    gotoAddEdit() {
      this.$router.push({ name: "industry_type_addEdit" });
    },
    getTableData() {
      this.tableData = [
        {
          Item1: "大分類",
          Item2: "A",
          Item3: "-",
          Item4: "農業、林業",
          Item5: "-",
          Item6: "使用",
          Item7: "2020/09/01",
          Item8: "2020/09/01",
          Item9: "編集",
        },
        {
          Item1: "中分類",
          Item2: "A",
          Item3: "01",
          Item4: "農業",
          Item5: "その他",
          Item6: "使用",
          Item7: "2020/09/01",
          Item8: "2020/09/01",
          Item9: "編集",
        },
        {
          Item1: "中分類",
          Item2: "A",
          Item3: "02",
          Item4: "林業",
          Item5: "その他",
          Item6: "未使用",
          Item7: "2020/09/01",
          Item8: "2020/09/01",
          Item9: "編集",
        },
      ];
      this.tableData.forEach((element, index) => {
        if (index === this.tableData.length - 1) {
          this.tableData[index].last_element = true;
          return;
        }
      });
    },
  },
};
</script>

<style>
.border-dashed {
  border: 1px dashed black;
}

.highLightHeaderLabel {
  font-weight: 700 !important;
  padding-top: 10px;
}

.countList {
  float: right;
  padding-top: 10px;
}
</style>